.new-user {
    background-color: #928e8e1a;
    border-bottom: 1px solid #282c3370;
    display: flex;
    flex-direction: column;
    padding: 15px;
}

.new-user button:first-child {
    margin-right: 4px;
}

.new-user-btn-box {
    text-align: right;
}
.task-list {
    height: 100%;
    max-height: 100%;
    overflow: auto;
    padding: 15px;
}

.task-list h5 {
    margin-left: 10px;
}

.task-list button.close {
    position: absolute;
    right: 10px;
    top: 5px;
}

.task-list button.edit {
    background-color: transparent;
    border: 0;
    bottom: 0px;
    opacity: 0.7;
    position: absolute;
    right: 5px;
}

.task-list button.edit:hover {
    cursor: pointer;
    font-weight: bold;
    opacity: 1;
}

.task-text {
    margin-left: 10px;
}
.App {
  height: 90vh;
}

.App-body {
  height: 100%;
  max-height: 100%;
  overflow: hidden;
}

.App-header {
  background-color: #282c34;
  color: #fff;
  display: flex;
  justify-content: space-between;
  height: 80px;
  padding: 1rem;
}

.App-header h1 {
  max-width: 60%;
}

.App-header nav {
  align-items: center;
  display: flex;
}

.App-header .btn:not(:first-child) {
  margin-left: 5px;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

